:root {
  --font-family: "Montserrat", sans-serif;
  --font-secondary: "TTNorms", sans-serif;
  --font-third: "Jost", sans-serif;
  --content-width: 100rem;
  --container-offset: 1.25rem;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));
  --fs-base: 1rem;
  --fw-base: 400;
  --lh-base: normal;
  --title-color: #000;
  --text-color: #414141;
  --primary: #ff7f00;
  --primary-300: #F3A354;
  --primary-400: #F19B3E;
  --secondary: #E60023;
  --secondary-500: #c5021f;
  --light: #fff;
  --gray: #7c7c7c;
  --body-bg: #fff;
  --rounded: 1rem;
  --shadow: 0px 0px .625rem .125rem rgba(0,0,0,0.375);
  --shadow-sm: 0px 0px .625rem .125rem rgba(0,0,0,0.25);
  --transition-duration: 300ms;
  --transition-timing-function: ease-in-out;
}

